import {
  MathRun,
  MathSuperScript,
  MathSubScript,
  MathRadical,
  MathFraction,
  MathSum,
} from "docx";
import { parseMath } from "@unified-latex/unified-latex-util-parse";
import type * as latex from "@unified-latex/unified-latex-types";
import { unreachable } from "./utils";

const hasSquareBrackets = (
  arg: latex.Argument | undefined
): arg is latex.Argument => {
  return !!arg && arg.openMark === "[" && arg.closeMark === "]";
};
const hasCurlyBrackets = (
  arg: latex.Argument | undefined
): arg is latex.Argument => {
  return !!arg && arg.openMark === "{" && arg.closeMark === "}";
};

const mapString = (s: string): MathRun => new MathRun(s);

const mapMacro = (n: latex.Macro, runs: MathRun[]): MathRun | false => {
  switch (n.content) {
    case "#":
      return mapString("#");
    case "$":
      return mapString("$");
    case "%":
      return mapString("%");
    case "&":
      return mapString("&");
    case "textasciitilde":
      return mapString("~");
    case "textasciicircum":
      return mapString("^");
    case "textbackslash":
      return mapString("‚àñ");
    case "{":
      return mapString("{");
    case "}":
      return mapString("}");
    case "textbar":
      return mapString("|");
    case "textless":
      return mapString("<");
    case "textgreater":
      return mapString(">");
    case "neq":
      return mapString("‚â†");
    case "sim":
      return mapString("‚àº");
    case "simeq":
      return mapString("‚âÉ");
    case "approx":
      return mapString("‚âà");
    case "fallingdotseq":
      return mapString("‚âí");
    case "risingdotseq":
      return mapString("‚âì");
    case "equiv":
      return mapString("‚â°");
    case "geq":
      return mapString("‚â•");
    case "geqq":
      return mapString("‚âß");
    case "leq":
      return mapString("‚â§");
    case "leqq":
      return mapString("‚â¶");
    case "gg":
      return mapString("‚â´");
    case "ll":
      return mapString("‚â™");
    case "times":
      return mapString("√ó");
    case "div":
      return mapString("√∑");
    case "pm":
      return mapString("¬±");
    case "mp":
      return mapString("‚àì");
    case "oplus":
      return mapString("‚äï");
    case "ominus":
      return mapString("‚äñ");
    case "otimes":
      return mapString("‚äó");
    case "oslash":
      return mapString("‚äò");
    case "circ":
      return mapString("‚àò");
    case "cdot":
      return mapString("‚ãÖ");
    case "bullet":
      return mapString("‚àô");
    case "ltimes":
      return mapString("‚ãâ");
    case "rtimes":
      return mapString("‚ãä");
    case "in":
      return mapString("‚àà");
    case "ni":
      return mapString("‚àã");
    case "notin":
      return mapString("‚àâ");
    case "subset":
      return mapString("‚äÇ");
    case "supset":
      return mapString("‚äÉ");
    case "subseteq":
      return mapString("‚äÜ");
    case "supseteq":
      return mapString("‚äá");
    case "nsubseteq":
      return mapString("‚äà");
    case "nsupseteq":
      return mapString("‚äâ");
    case "subsetneq":
      return mapString("‚ää");
    case "supsetneq":
      return mapString("‚äã");
    case "cap":
      return mapString("‚à©");
    case "cup":
      return mapString("‚à™");
    case "emptyset":
      return mapString("‚àÖ");
    case "infty":
      return mapString("‚àû");
    case "partial":
      return mapString("‚àÇ");
    case "aleph":
      return mapString("‚Ñµ");
    case "hbar":
      return mapString("‚Ñè");
    case "wp":
      return mapString("‚Ñò");
    case "Re":
      return mapString("‚Ñú");
    case "Im":
      return mapString("‚Ñë");
    case "alpha":
      return mapString("Œ±");
    case "beta":
      return mapString("Œ≤");
    case "gamma":
      return mapString("Œ≥");
    case "delta":
      return mapString("Œ¥");
    case "epsilon":
      return mapString("œµ");
    case "zeta":
      return mapString("Œ∂");
    case "eta":
      return mapString("Œ∑");
    case "theta":
      return mapString("Œ∏");
    case "iota":
      return mapString("Œπ");
    case "kappa":
      return mapString("Œ∫");
    case "lambda":
      return mapString("Œª");
    case "eta":
      return mapString("Œ∑");
    case "mu":
      return mapString("Œº");
    case "nu":
      return mapString("ŒΩ");
    case "xi":
      return mapString("Œæ");
    case "pi":
      return mapString("œÄ");
    case "rho":
      return mapString("œÅ");
    case "sigma":
      return mapString("œÉ");
    case "tau":
      return mapString("œÑ");
    case "upsilon":
      return mapString("œÖ");
    case "phi":
      return mapString("œï");
    case "chi":
      return mapString("œá");
    case "psi":
      return mapString("œà");
    case "omega":
      return mapString("œâ");
    case "varepsilon":
      return mapString("Œµ");
    case "vartheta":
      return mapString("œë");
    case "varrho":
      return mapString("œ±");
    case "varsigma":
      return mapString("œÇ");
    case "varphi":
      return mapString("œÜ");
    case "Gamma":
      return mapString("Œì");
    case "Delta":
      return mapString("Œî");
    case "Theta":
      return mapString("Œò");
    case "Lambda":
      return mapString("Œõ");
    case "Xi":
      return mapString("Œû");
    case "Pi":
      return mapString("Œ†");
    case "Sigma":
      return mapString("Œ£");
    case "Upsilon":
      return mapString("Œ•");
    case "Phi":
      return mapString("Œ¶");
    case "Psi":
      return mapString("Œ®");
    case "Omega":
      return mapString("Œ©");
    case "newline":
    case "\\":
      // line break
      return false;
    case "^": {
      const prev = runs.pop();
      if (!prev) break;
      return new MathSuperScript({
        children: [prev],
        superScript: mapGroup(n.args?.[0]?.content ?? []),
      });
    }
    case "_": {
      const prev = runs.pop();
      if (!prev) break;
      return new MathSubScript({
        children: [prev],
        subScript: mapGroup(n.args?.[0]?.content ?? []),
      });
    }
    case "hat":
      // TODO: implement
      break;
    case "widehat":
      // TODO: implement
      break;
    case "sum": {
      // TODO: support superscript and subscript
      return new MathSum({
        children: [],
      });
    }
    case "int":
      return mapString("‚à´");
    case "frac":
    case "tfrac":
    case "dfrac": {
      const args = n.args ?? [];
      if (
        args.length === 2 &&
        hasCurlyBrackets(args[0]) &&
        hasCurlyBrackets(args[1])
      ) {
        return new MathFraction({
          numerator: mapGroup(args[0].content),
          denominator: mapGroup(args[1].content),
        });
      }
      break;
    }
    case "sqrt": {
      const args = n.args ?? [];
      if (args.length === 1 && hasCurlyBrackets(args[0])) {
        return new MathRadical({
          children: mapGroup(args[0].content),
        });
      }
      if (
        args.length === 2 &&
        hasSquareBrackets(args[0]) &&
        hasCurlyBrackets(args[1])
      ) {
        return new MathRadical({
          children: mapGroup(args[1].content),
          degree: mapGroup(args[0].content),
        });
      }
      break;
    }
    default:
      break;
  }
  return mapString(n.content);
};

const mapGroup = (nodes: latex.Node[]): MathRun[] => {
  const group: MathRun[] = [];
  for (const c of nodes) {
    group.push(...(mapNode(c, group) || []));
  }
  return group;
};

const mapNode = (n: latex.Node, runs: MathRun[]): MathRun[] | false => {
  switch (n.type) {
    case "root":
      break;
    case "string":
      return [mapString(n.content)];
    case "whitespace":
      break;
    case "parbreak":
      break;
    case "comment":
      break;
    case "macro":
      const run = mapMacro(n, runs);
      if (!run) {
        // line break
        return false;
      } else {
        return [run];
      }
    case "environment":
    case "mathenv":
      break;
    case "verbatim":
      break;
    case "inlinemath":
      break;
    case "displaymath":
      break;
    case "group":
      return mapGroup(n.content);
    case "verb":
      break;
    default:
      unreachable(n);
  }

  return [];
};

export const parseLatex = (value: string): MathRun[][] => {
  const parsed = parseMath(value);
  const paragraphs: MathRun[][] = [[]];
  let runs: MathRun[] = paragraphs[0]!;
  for (const n of parsed) {
    const res = mapNode(n, runs);
    if (!res) {
      // line break
      paragraphs.push((runs = []));
    } else {
      runs.push(...res);
    }
  }
  return paragraphs;
};
